<template>
    <v-app>
       <v-card
    color="grey lighten-4"
    flat
    tile
  >
   <v-toolbar absolute>
      <v-toolbar-title class="px-4">Search</v-toolbar-title>
      <v-toolbar-items>
      <v-text-field
      class="px-4"
        v-model="searchInput"
        hint="Search something !"
        @keyup.enter="getMarkers()"
      ></v-text-field>

      <v-btn icon @click="geolocate()" class="px-4">
        <v-icon>mdi-crosshairs-gps</v-icon>
      </v-btn>

      <v-btn icon @click="getMarkers()" class="px-4">
        <v-icon>mdi-magnify</v-icon>
      </v-btn>
      </v-toolbar-items>
    </v-toolbar>
 </v-card>
 


        <gmap-map
        
      :center="center"
      :zoom="12"
      style="width:100%;  height: 80%;"
    >
      <gmap-info-window 
        :options="infoOptions" 
        :position="infoWindowPosition" 
        :opened="showInfoWindow" 
        @closeclick="showInfoWindow=false"/>
      
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        @click="openInfoWindow(m, index)"
      />
    </gmap-map>
    </v-app>
</template>

<script>
import axios from 'axios';

export default {
    name: "MapComponent",
    data() {
    return {
      center: { lng: 17.034303, lat: 51.110440 },
      markers: [],
      places: [],
      currentPlace: null,
      showInfoWindow: false,
      infoWindowPosition: null,
      currentInfoWindowIdx: null,
      searchInput: null,
      infoOptions: {
        content: '',
          //optional: offset infowindow so it visually sits nicely on top of our marker
          pixelOffset: {
            width: 0,
            height: -35
            }
      },
    }
    },
    mounted(){
      //this will set center of the map on user location
      //this.geolocate();
    },
    methods: {
      geolocate() {
      navigator.geolocation.getCurrentPosition(position => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      })
      },
      openInfoWindow(marker, index){
        this.infoWindowPosition = marker.position;
        this.getMenu(marker).then(result => {
          this.infoOptions.content = result;
        })
        
        
        if(this.currentInfoWindowIdx == index){
          this.showInfoWindow = !this.showInfoWindow;
        }
        else{
          this.showInfoWindow = true;
          this.currentInfoWindowIdx = index;
        }
      },
      getMarkers(){
        this.markers = []
        let places = []
        //let res = axios.get('https://localhost:44340/api/restaurant/GetPlacesWithMeal/' + this.searchInput)
        axios.get('https://localhost:44340/api/restaurant/GetPlacesWithMeal/Test Meal')
        .then((response) => {
            response.data.forEach(element => {
              places.push({
                position: {
                lng: element.lng,
                lat: element.lat
              },
              infoText: element.name
              })
            })
        }, (error) => {
          console.log(error);
        });
        this.markers = places
      },
      async getMenu(marker){
        var menu = '<p>' + marker.infoText + '</p>';
        await axios.get('https://localhost:44340/api/restaurant/getrestaurant/' + marker.infoText + '/Menu')
        .then((response) => {
          menu = menu + '<p>'
          response.data.forEach(element => {
            menu = menu + '<br> Name: '+ element.name+ 'Price: '+ element.price +'</br>'
          })
          menu = menu + '</p>'
        }, (error) => {
          console.log(error)
        });
        return menu
      }
    }
    
    
    
}
</script>