<template>
  <v-app>
    <v-card class="ma-12">
      <v-container>
        <v-form v-model="valid" ref="form" :lazy-validation="true" class="ma-5">
          <v-row justify="center">
            <v-col cols="8">
              <v-text-field v-model="name" label="Name" :rules="nameRules" :counter="25" required></v-text-field>
            </v-col>
          </v-row>
          <v-row justify="center">
            <v-col cols="4">
              <v-text-field v-model="address_street" label="Street" :rules="streetRules" :counter="25"  required></v-text-field>
            </v-col>
            <v-col cols="4">
              <v-text-field v-model="address_number" label="Number" :rules="numberRules" required></v-text-field>
            </v-col>
          </v-row>
          <v-row justify="center">
            <v-col cols="4">
              <v-text-field v-model="address_postal_code" label="Postal Code" :rules="codeRules" :counter="25"  required></v-text-field>
            </v-col>
            <v-col cols="4">
              <v-text-field v-model="address_city" label="City" :rules="cityRules" required></v-text-field>
            </v-col>
          </v-row>
          <v-row justify="center">
            <v-col cols="4"></v-col>
            <v-col cols="4">
              <v-text-field v-model="address_country" label="Country" :rules="codeRules" required></v-text-field>
            </v-col>
          </v-row>
          <v-row justify="center">
            <v-col cols="8">
              <v-text-field v-model="login" label="Login" :rules="loginRules" required></v-text-field>
            </v-col>
          </v-row>
          <v-row justify="center">
            <v-col cols="8">
              <v-text-field v-model="password" label="Password" :rules="passwordRules" required></v-text-field>
            </v-col>
          </v-row>
          <v-row justify="center">
            <v-col cols="8">
              <v-text-field v-model="second_password" label="Repeat password" :rules="secondPasswordRules" required></v-text-field>
            </v-col>
          </v-row>
          <v-row justify="center">
            <v-btn class="ma-1" @click="clearInput()">Clear</v-btn>
            <v-btn :disabled="!valid" class="ma-1" @click="register()">Register</v-btn>
          </v-row>
        </v-form>
      </v-container>
    </v-card>
  </v-app>
</template>

<script>
import axios from 'axios';
export default {
  name: "RegisterForm",
  data() {
    return {
      valid: true,
      name: "",
      lng: 0.0,
      lat: 0.0,
      address_street: "",
      address_number:"",
      address_postal_code:"",
      address_city:"",
      address_country: "",
      login: "",
      password: "",
      second_password: "",
      nameRules: [
        v => !!v || "Name is required",
        v => (v && v.length <= 10) || "Name must be less than 10 characters"
      ],
      streetRules: [
        v => !!v || "Street is required"
      ],
      numberRules: [
        v => !!v || "Number is required"
      ],
      codeRules: [
        //v => !!v || "Postal code is required"
      ],
      cityRules: [
        v => !!v || "City is required"
      ],
      countryRules: [
        v => !!v || "Country is required"
      ],
      loginRules: [
        v => !!v || "Login is required"
      ],
      passwordRules: [
        v => !!v || "Password is required"
      ],
      secondPasswordRules: [
        v => !!v || "Password is required",
        () => (this.second_password == this.password) || "Confirmation should be the same as password"
      ]
      
    };
  },
  
  methods: {
    validate () {
      if (this.$refs.form.validate()) {
        this.snackbar = true
      }
    },
    clearInput() {
      this.$refs.form.reset()
    },
    register(){
      if(this.$refs.form.validate()){
        if(this.password != this.second_password){
          //this.$refs.form.reset()
        }else{
          this.geocode().then( () =>
            console.log({
              name: this.name,
              lng: this.lng,
              lat: this.lat,
              login: this.login,
              password: this.password
            })
          );
        }
        
      }
    },
    async geocode() {
      await axios.get(
        'https://maps.googleapis.com/maps/api/geocode/json?' + 'address='+ this.address_street + this.address_number + this.address_city + this.address_postal_code + this.address_country + '&key=AIzaSyCc1phOdSc1-2-v_mLiNV9Fb4u6lvlPG3k' 
        ).then(response => {
          this.lat = response.data.results[0].geometry.location.lat;
          this.lng = response.data.results[0].geometry.location.lng;
          console.log(this.lat)
          console.log(this.lng)
        }, error => {
          console.log(error);
        });
    }
  }
};
</script>

